/* --------------- */
.TaskRowCont {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    /* justify-content: center; */
    align-items: center;
    background: #FFFFFF;
    box-shadow: 0.313rem 0.313rem 0.563rem rgba(224, 224, 224, 0.7);
    /* 5px, 5px, 9px to REM */
    border-radius: 0.188rem;
    /* 3px to REM */
    position: relative;
    height: 100%;
    flex: 1 0 100%;
    transition: all 0.5s;
}

.TaskRowCont:hover {
    filter: drop-shadow(1px 3px 4px gray);
}

.checkBox {
    position: absolute;
    opacity: 0;
    pointer-events: none;
}

input[type="checkbox"]+label {
    position: relative;
    padding-left: 1.875rem;
    /* 30px to REM */
    /* Adjust as needed for the checkbox size */
    cursor: pointer;
}

input[type="checkbox"]+label::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 1rem;
    /* 16px to REM */
    /* Adjust as needed for the checkbox size */
    height: 1rem;
    /* 16px to REM */
    /* Adjust as needed for the checkbox size */
    /* border: 0.125rem solid #000; */
    /* 2px solid #000; to REM */
    background-color: #fff;
    border: 0.188rem solid #6074F9;
    /* 3px solid #6074F9 to REM */
    border-radius: 50%;
}

/* Style the custom checkbox when checked */
input[type="checkbox"]:checked+label::before {
    /* background-color: #000; */
    border: 0.188rem solid #6074F900;
    /* 3px solid #6074f900 to REM */
    background: #F96060;
}

input[type="checkbox"]:checked+label::before {
    content: '\2713';
    color: #fff;
    font-size: 0.75rem;
    /* 12px to REM */
    line-height: 1rem;
    /* 16px to REM */
    text-align: center;
}

.taskText {
    user-select: none;
    margin: 0px 0px 0px 0.938rem;
    /* 0px 0px 0px 15px to REM */
    width: 100%;
    cursor: pointer;
}

.taskTitle {
    font-size: 1rem;
    /* 16px to REM */
    line-height: 1.19rem;
    /* 19px to REM */
    color: #313131;
    height: 1.25rem;
    /* 20px to REM */
    overflow: hidden;
    padding-right: 1.25rem;
    /* 20px to REM */
    text-align: start;
    font-weight: 600;
}

.taskTitle.disapled {
    color: #9B9B9B;
    font-weight: 500;
    text-decoration: line-through;
}

.taskTime {
    font-size: 1rem;
    /* 16px to REM */
    line-height: 1.19rem;
    /* 19px to REM */
    text-align: start;
    color: #9B9B9B;
}

.taskColorDiv {
    width: 0.25rem;
    /* 4px to REM */
    height: 1.31rem;
    /* 21px to REM */
    position: absolute;
    right: 0px;
    cursor: pointer;
}

/* --------------- */

.outerWarper {
    display: flex;
    flex-direction: row;
    align-items: center;
    transform: translateX(0%);
    transition: all 0.5s;
    height: 4.38rem;
    /* 70px to REM */
    box-sizing: border-box;
    margin-bottom: 1rem;
    /* overflow-x: visible; */
}

.outerWarper.showOp {
    transform: translateX(-8.88rem);
    /* -30px to REM */
    /* overflow-x: hidden; */
}

.editDiv {
    /* border: 0.063rem solid black; */
    /* 1px solid black to REM */
    display: flex;
    justify-content: center;
    align-items: center;
    /* cursor: pointer; */
    box-shadow: 0.313rem 0.313rem 0.563rem rgba(224, 224, 224, 0.7);
    /* 5px 5px 9px to REM */
    height: 100%;
    flex: 1 0 3.75rem;
    background-color: white;
    margin-left: 0.2rem;
    /* 60px to REM */
}

.editDiv,
.deleteDiv {
    transition: all 0.5s;
}

.editDiv:hover,
.deleteDiv:hover {
    filter: drop-shadow(1px 3px 4px gray);

}

.deleteDiv {
    /* border: 0.063rem solid black; */
    /* 1px solid black to REM */
    display: flex;
    justify-content: center;
    align-items: center;
    /* cursor: pointer; */
    box-shadow: 0.313rem 0.313rem 0.563rem rgba(224, 224, 224, 0.7);
    /* 5px 5px 9px to REM */
    height: 100%;
    flex: 1 0 3.75rem;
    background-color: white;
    margin-left: 0.2rem;
    /* 60px to REM */
}

.editDiv>img,
.deleteDiv>img {
    cursor: pointer;
    width: auto;
    transition: all 0.5s;
    transform: scale(1.3);
}

