@media (min-width: 414px) {
    /* .navContainer {
        height: 1.92rem; */
    /* 12% converted to REM */
    /* } */
}

.btnsWarper {
    height: 100%;
    width: 100%;
    /* position: relative; */
    background-color: #292E4E;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    /* color: #8E8E93; */
}

.btnDiv.active .btnSpan {
    color: white;
}

.btnDiv {
    width: 5rem;
    /* height: 100%; */
    padding-top: 0.063rem;
    /* 1px to REM */
}

.btnDiv:hover a {
    filter: brightness(10);
}

.btnImg {
    /* max-width: 100%; */
    /* object-fit: scale-down; */
    cursor: pointer;
    width: 100%;
    max-height: 1.406rem;
    /* 45% converted to REM */
}

.btnSpan {
    display: block;
    /* 0.75rem  */
    /* 12px to REM */
    font-size: 1rem;
    /* 0.875rem  */
    /* 14px to REM */
    line-height: 1rem;
    text-align: center;
    letter-spacing: 0.12px;
    color: #8E8E93;
    cursor: pointer;
    margin-top: 0.031rem;
    /* 0.5px to REM */
}

.addBtnDiv {
    height: 100%;
    position: relative;
    flex-basis: 3.75rem;
    /* 60px to REM */
}

.addBtnDivCer {
    width: 3.5rem;
    /* 56px to REM */
    height: 3.5rem;
    /* 56px to REM */
    /* background: red; */
    border-radius: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -1rem;
    /* -16px to REM */
    background: radial-gradient(98.92% 98.92% at 95.32% 45.48%, #F96060 0%, #F68888 100%), #F96060;
    /* 1.5rem  */
    /* 24px to REM */
    font-size: calc(1rem + 1.5rem);
    /* 8px to REM */
    line-height: 1.75rem;
    /* 28px to REM */
    text-align: center;
    letter-spacing: 0.24px;
    color: #FFFFFF;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;

    cursor: pointer;
}

.addBtnDivCer span {
    transition: all 0.5s;
}

.addBtnDivCer:hover span {
    transform: rotateZ(180Deg);
}

.dNone {
    display: none;
}