.groupDiv {
    background: #FFFFFF;
    box-shadow: 0px 2px 10px rgba(227, 227, 227, 0.5);
    border-radius: 5px;
    width: 100%;
    height: 9.375rem;
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-end;
    transition: all 0.5s;
}

.groupDiv:hover {
    filter: drop-shadow(1px 3px 4px gray);
}

.groupColorDiv {
    width: 0.875rem;
    height: 0.875rem;
    line-height: 0.875rem;
    display: inline-block;
    /* background-color: #6074F9; */
    border-radius: 50%;
    position: absolute;
    left: 1.25rem;
    top: 1.25rem;
    /* outline: 6px solid #DBDDEF; */
}

.groupTitle {
    font-size: 1.125rem;
    line-height: 1.3125rem;
    text-align: start;
    color: #313131;
    margin: 0px;
    flex: 1 0 100%;
    padding: 0.625rem 1.25rem;
    overflow: hidden;
}

.countLabel {
    font-size: 1rem;
    line-height: 1.1875rem;
    text-align: start;
    display: block;
    color: #9A9A9A;
    margin-top: 0.625rem;
}

.optionsWarper {
    position: absolute;
    right: 5px;
    height: 0%;
    width: 3rem;

    top: 50%;
    transform: translateY(-50%);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    transition: all 0.5s ease-in-out
}

.optionsWarper.open {
    height: 90%;
}

.optionsWarper div {
    display: none;
    font-size: 1.3rem;
    width: 1.3rem;
}

.optionsWarper div img {
    width: 100%;
    cursor: pointer;
    transition: all 0.5s;
}

.optionsWarper div img:hover {
    transform: scale(1.3);
}

.optionsWarper.open div {
    display: initial;
}

.checkedIcon::before {
    content: '\2713';
    color: #fff;
    font-size: 1rem;
    line-height: 1.3rem;
    text-align: center;
    background: #f96060;
    width: 1.3rem;
    height: 1.3rem;
    border: 0.1rem solid #6074F900;
    border-radius: 50%;
    display: block;
    cursor: pointer;
    transition: all 0.5s;
}

.checkedIcon:hover::before {
    transform: scale(1.3);
}