.menuWarpper {
    width: 100%;
    height: calc(100% - (17% + 2%));
    padding: 5% 5%;
    box-sizing: border-box;
    /* position: relative; */
    background: whitesmoke;

    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    align-content: flex-start;
    /* justify-items: center; */

    overflow-y: scroll;
    overflow-x: hidden;
}

@media (max-width: 600px) {
    .menuWarpper {
        height: calc(100% - (21%));
    }
}

.groupDiv {
    background: #FFFFFF;
    box-shadow: 0px 2px 10px rgba(227, 227, 227, 0.5);
    border-radius: 5px;
    width: 100%;
    height: 150px;
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-end;

}


.groupColorDiv {
    width: 14px;
    height: 14px;
    line-height: 14px;
    display: inline-block;
    /* background-color: #6074F9; */
    border-radius: 50%;
    position: absolute;
    left: 20px;
    top: 20px;
    /* outline: 6px solid #DBDDEF; */
}

.groupTitle {
    font-size: 18px;
    line-height: 21px;
    text-align: start;
    color: #313131;
    margin: 0px;
    flex: 1 0 100%;
    padding: 10px 20px;
    overflow: hidden;
}

.countLabel {
    font-size: 16px;
    line-height: 19px;
    text-align: start;
    display: block;
    color: #9A9A9A;
    margin-top: 10px;
}

.addGroupDiv {
    border-radius: 5px;
    width: 47.5%;
    height: 80px;
    /* border: 1px solid black; */
    background-color: #6074F9;
    color: white;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

.addGroupBtn {
    background: none;
    outline: none;
    border: none;
    cursor: pointer;
    font-size: 24px;
    line-height: 28px;
    /* identical to box height */
    text-align: center;
    letter-spacing: 0.24px;

    color: #FFFFFF;
}

.editContaineer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background: #00000061;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

.editInnerContaineer {
    width: 60%;
    padding: 1rem;
    border: 0.063rem solid white;
    height: auto;
    background: white;
    color: black;
    box-shadow: 0.063rem 0.063rem 1.25rem 0px rgba(224, 224, 224, 0.7);
    border-radius: 0.313rem;
}