.fullWarper {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    border: 1px solid black;
    box-sizing: border-box;
    text-align: center;
}

.topPart {
    width: 100%;
    height: 70%;
}

.bottomPart {
    width: 100%;
    height: 30%;
}

.bottomInnerDiv {
    width: 100%;
    height: 100%;
    /* background: url(http://localhost:5173/photos/landingBottom.svg); */
    /* background-size: cover;
    position: relative; */
}

.getStartedBtn {
    filter: drop-shadow(10px 10px 10px rgba(200, 68, 68, 0.5));
    background: #FFFFFF;
    border-radius: 5px;
    font-family: 'AvenirNextRoundedPro';
    font-size: 18px;
    line-height: 21px;
    color: #313131;
    /* padding: 10px 20px; */
    width: 293px;
    height: 48px;
    border: none;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 35%;
    cursor: pointer;
}

.dNone {
    display: none;
}