.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* margin-top: 1.25rem; */
}

.errorParagraph {
    color: red;
    margin-bottom: 0.625rem;
}


.AddQuickForm {
    width: 100%;
    padding: 0.625rem;
}

.formInput {
    width: 90%;
    padding: 0.313rem;
    margin-bottom: 0.625rem;
    font-size: 1rem;
    border: 0.063rem solid #ccc;
    border-radius: 0.188rem;
}

.labelTag {
    font-weight: bold;
    text-align: start;
    display: block;
    margin-bottom: 0.313rem;
}

.inputDiv {
    margin-bottom: 0.625rem;
}

.groupOption {
    color: white;
}

.groupPickerDiv {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 1rem 0rem;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.groupOptionDiv {
    width: 6rem;
    height: 3rem;
    border-radius: 0.31rem;
    cursor: pointer;
    position: relative;
    text-align: center;
    line-height: 3rem;
    font-size: 1rem;
    transition: all 0.5s;
}

.groupOptionDiv.selected {
    color: white;
    font-size: 1.2rem;
    font-weight: bold;
    border: 1px solid black;
}

.submitInput {
    width: 100%;
    padding: 0.5rem 0.625rem;
    font-size: 1rem;
    color: white;
    background-color: #292E4E;
    border: none;
    border-radius: 0.188rem;
    cursor: pointer;
}

.submitDiv {
    display: flex;
    justify-content: center;
}

.updateInput {
    width: 30%;
    margin: 0.5rem 1rem;
    padding: 0.5rem 0.625rem;
    /* 8px, 10px in rem */
    font-size: 1rem;
    /* 16px in rem */
    color: white;
    background-color: #292E4E;
    border: none;
    border-radius: 0.188rem;
    /* 3px in rem */
    cursor: pointer;
}

.cancelInput {
    width: 30%;
    margin: 0.5rem 1rem;
    padding: 0.5rem 0.625rem;
    /* 8px, 10px in rem */
    font-size: 1rem;
    /* 16px in rem */
    color: white;
    background-color: #f33e4d;
    border: none;
    border-radius: 0.188rem;
    /* 3px in rem */
    cursor: pointer;
}