.outerWarper {
    width: 100%;
    /* height: fill-available; (100% - (151px + 71px)) */
    /* height: calc(100% - (151px + 71px));*/
    height: calc(100% - (20% + 12%));
    /* 17% and 6% converted to REM */
    /* 21 on small screens */
    padding-top: 0.8rem;
    /* 5% converted to REM */
    box-sizing: border-box;
    background: whitesmoke;
    /* margin-top: 2rem; */
}

