.todayDateP {
    text-align: start;
    font-size: calc(1rem + 0.13rem);
    /* 18px to REM */
    line-height: 1rem;
    /* 16px to REM */
    text-transform: uppercase;
    height: 0.48rem;
    /* 3% converted to REM */
    color: #9A9A9A;
    padding: 0% 0.125rem;
    /* 2px to REM */
    margin: 0px;
    margin-bottom: 2rem;
}

.allTasksRows {
    height: calc(100% - 0.48rem);
    /* 3% converted to REM */
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 0% 1rem;
    /* 2px to REM */
}



.editContaineer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #00000061;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

.editInnerContaineer {
    width: 60%;
    padding: 1rem;
    border: 0.063rem solid white;
    height: auto;
    background: white;
    color: black;
    box-shadow: 0.063rem 0.063rem 1.25rem 0px rgba(224, 224, 224, 0.7);
    border-radius: 0.313rem;
}