.NewTaskOuterWarper {
    width: 100%;
    height: 100%;
    background: #00000066;
    position: absolute;
    top: 0px;
    z-index: 100;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

.NewTaskInerWarper {
    width: 60%;
    padding: 1rem;
    /* 75% to REM */
    border: 0.063rem solid white;
    /* 1px to REM */
    height: auto;
    background: white;
    color: black;
    box-shadow: 0.063rem 0.063rem 1.25rem 0px rgba(224, 224, 224, 0.7);
    /* 1px, 1px, 20px to REM */
    border-radius: 0.313rem;
    /* 5px to REM */
}

.NewTaskInerWarper div.addDiv {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    height: 3.75rem;
    /* 60px to REM */
    border-top: 0.125rem solid #E4E4E4;
    /* 2px to REM */
}

.NewTaskInerWarper div.addDiv.addTaskDiv {
    border-top: none;
}

.NewTaskInerWarper div span.addSpan {
    font-size: 1.125rem;
    /* 18px to REM */
    line-height: 1.313rem;
    /* 21px to REM */
    color: #313131;
    cursor: pointer;
}