.wrapper {
    /* width: 28.13rem; */
    /* 450px to REM */
    background: #fff;
    border-radius: 0.625rem;
    /* 10px to REM */
    box-shadow: 0 0.938rem 2.5rem rgba(0, 0, 0, 0.12);
    /* 0px, 15px, 40px to REM */
    margin-bottom: 1rem;
}

.wrapper header {
    display: flex;
    align-items: center;
    padding: 0px 1.438rem 0px;
    /* 23px to REM */
    justify-content: space-between;
}

.wrapper header p {
    font-size: 1.5rem;
}

header .icons {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    width: 2.5rem;
    /* 40px to REM */
}

header .icons span {
    height: 1.25rem;
    /* 20px to REM */
    width: 1.25rem;
    /* 20px to REM */
    margin: 0 0.063rem;
    /* 1px to REM */
    cursor: pointer;
    color: #878787;
    text-align: center;
    /* line-height: 2.375rem; */
    /* 38px to REM */
    /* font-size: 1.9rem; */
    user-select: none;
    border-radius: 50%;
}

header .icons span img {
    width: 100%;
}

.icons span:last-child {
    margin-right: -0.625rem;
    /* -10px to REM */
}

header .icons span:hover {
    background: #f2f2f2;
}

header .current-date {
    font-size: 1.45rem;
    /* 23px to REM */
    font-weight: 500;
}

.calendar {
    padding-bottom: 1.25rem;
    /* 20px to REM */
}

.calendar ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    text-align: center;
    padding: 0px;
}

.calendar .days {
    margin-bottom: 1.25rem;
    /* 20px to REM */
}

.calendar li {
    color: #333;
    width: calc(100% / 7);
    font-size: 1rem;
    /* 16px to REM */
}

.calendar .weeks li {
    font-weight: 400;
    cursor: default;
    color: #aaa;
}

.calendar .days li {
    z-index: 1;
    cursor: pointer;
    position: relative;
    margin-top: 1.875rem;
    /* 30px to REM */
}

.days li.inactive {
    color: #aaa;
}

.days li.active {
    color: #fff;
}

.days li::before {
    position: absolute;
    content: "";
    left: 50%;
    top: 50%;
    height: 2.5rem;
    /* 40px to REM */
    width: 2.5rem;
    /* 40px to REM */
    z-index: -1;
    border-radius: 50%;
    transform: translate(-50%, -50%);
}

.days li.active::before {
    background: #9e9e9eb3;
}

.days li::after {
    content: '';
    display: block;
    width: 0.313rem;
    /* 5px to REM */
    height: 0.313rem;
    /* 5px to REM */
    /* background: red; */
    border-radius: 50%;
    position: absolute;
    bottom: -0.625rem;
    /* -10px to REM */
    left: 50%;
    transform: translate(-50%, -50%);
}

.days li.activeTask::after {
    background: #F96060;
}

.days li:not(.active):hover::before {
    background: #f2f2f2;
}