.outerCont {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 100%;
}

.btnCont {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
    /* cursor: pointer; */
}

.btnCont.prev {
    left: 10px;
}

.btnCont.next {
    right: 10px;
}

button.btn {
    background: #0000005e;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    outline: none;
    color: white;
    font-size: 25px;
    font-weight: 600;
}

.imgsWarper {
    display: flex;
    flex-direction: row;
    align-content: center;
    align-items: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.slideDiv {
    flex: 0 0 0%;
    height: 100%;
    width: 100%;
    position: relative;
    opacity: 0;
    transition: all 0.5s ease-in-out;
}

.slideDiv>.titleDiv,
.slideDiv>.textDiv {
    display: none;
    transition: all 0.5;
}

.slideDiv.active>.titleDiv,
.slideDiv.active>.textDiv {
    display: initial;
}

.slideDiv.active {
    flex: 0 0 100%;
    opacity: 1;
}

