.headerWarper {
    background: #F96060;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    color: white;
    /* height: 9.44rem; */
    /* 151px to REM */
    height: 20%;
    position: relative;
    /* z-index: 1; */
}

