.headerWarperProjects {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    color: white;
    /* height: 6.25rem; */
    height: 10%;
    /* 100px to REM */
    position: relative;
    background: #FFFFFF;
    box-shadow: 0px 2px 10px rgba(227, 227, 227, 0.5);
    z-index: 1;
}

.headerWarperProjects p {
    font-size: 1.25rem;
    /* 20px to REM */
    line-height: 2.56rem;
    /* 41px to REM */
    text-align: center;
    color: #313131;
}

.HeaderRow1 {
    width: 100%;
    text-align: center;
}

.headerTitle {
    font-size: calc(1rem + 0.25rem);
    /* 20px to REM */
    line-height: 2.56rem;
    /* 41px to REM */
    text-align: center;
    color: #FFFFFF;
    padding: 0px;
    margin: 1rem;
}

.OptionsDiv {
    width: 100%;
    height: 100vh;
    /* 56.48px to REM */
    position: absolute;
    top: 0;
    right: 0;
    background: #00000057;
    display: none;
    overflow: hidden;
    z-index: 100;
}

.showOpt {
    display: initial;
}

.optionsIconWarper {
    position: absolute;
    top: 2.5rem;
    /* 40px to REM */
    right: 0.63rem;
    /* 10px to REM */
    width: 1.5rem;
}

.OptionsIcon {

    width: 100%;
    /* 20px to REM */
    /* height: 1.31rem; */
    /* 21px to REM */
    cursor: pointer;
}

.OptionsList {
    list-style: none;
    position: absolute;
    top: 3.13rem;
    /* 50px to REM */
    right: 0.63rem;
    /* 10px to REM */
    z-index: 1;
    color: black;
    padding: 0px;
    min-width: 14.5rem;
    /* 232px to REM */
    background: #FFFFFF;
    border-radius: 0.31rem;
    /* 5px to REM */
    box-shadow: 0px 0px 0.19rem 0px #9E9E9E;
    transition: all 0.5s;
    transform: translateX(15rem);
    /* 108% to REM */

}

.showOptionsList {
    transform: translateX(0%);
}

.OptionsList li {
    width: 100%;
    font-size: calc(1rem + 0.13rem);
    /* 18px to REM */
    line-height: 1.25rem;
    /* 20px to REM */
    color: #313131;
    margin: 0.63rem auto;
    /* 10px to REM */
    text-align: start;
    padding: 0px 0.63rem;
    /* 10px to REM */
    box-sizing: border-box;
    cursor: pointer;
}

.optionsSelectedIcon {
    float: right;
    margin-right: 0.31rem;
    /* 5px to REM */
}

.HeaderRow2 {
    width: 100%;
}

.tapsDiv {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.tabDiv {
    width: 4.38rem;
    /* 70px to REM */
    cursor: pointer;
}

.tabDiv.active {
    border-bottom: 0.19rem solid white;
    /* 3px to REM */
}

.tabDiv p {
    margin: 0px;
    margin-bottom: 0.63rem;
    /* 10px to REM */
    font-family: 'SFProText';
    font-size: calc(1rem + 0.13rem);
    /* 18px to REM */
    line-height: 1rem;
    /* 16px to REM */
    text-align: center;
    letter-spacing: 0.05rem;
    /* -0.28px to REM */
    color: #FFFFFF;
}

.dNone {
    display: none;
}