.quickWarpper {
    height: calc(100% - (12% + 10%));
    width: 100%;
    padding-top: 1rem;
    overflow-y: scroll;
    padding-bottom: 1rem;
    box-sizing: border-box;
    background: whitesmoke;
}

.editContaineer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background: #00000061;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

.editInnerContaineer {
    width: 60%;
    padding: 1rem;
    border: 0.063rem solid white;
    height: auto;
    background: white;
    color: black;
    box-shadow: 0.063rem 0.063rem 1.25rem 0px rgba(224, 224, 224, 0.7);
    border-radius: 0.313rem;
}