/* styles.module.css */

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* margin-top: 1.25rem; */
    /* 20px in rem */
}

.errorParagraph {
    color: red;
    margin-bottom: 0.625rem;
    /* 10px to REM */
}

.AddTaskForm {
    /* width: 18.75rem; */
    /* 300px in rem */
    /* padding: 0.625rem; */
    /* 10px in rem */
    /* border: 0.063rem solid #ccc; */
    /* 1px in rem */
    /* border-radius: 0.313rem; */
    /* 5px in rem */
    width: 100%;
}

.formInput {
    width: 90%;
    padding: 0.313rem;
    /* 5px in rem */
    margin-bottom: 0.625rem;
    /* 10px in rem */
    font-size: 1rem;
    /* 16px in rem */
    border: 0.063rem solid #ccc;
    /* 1px in rem */
    border-radius: 0.188rem;
    /* 3px in rem */
}

.labelTag {
    font-weight: bold;
    text-align: start;
    display: block;
    margin-bottom: 0.313rem;
    /* 5px in rem */
}

.submitInput {
    width: 100%;
    padding: 0.5rem 0.625rem;
    /* 8px, 10px in rem */
    font-size: 1rem;
    /* 16px in rem */
    color: white;
    background-color: #292E4E;
    border: none;
    border-radius: 0.188rem;
    /* 3px in rem */
    cursor: pointer;
}

.colorInput {
    margin-bottom: 1rem;
}

.updateInput {
    width: 30%;
    margin: 0.5rem 1rem;
    padding: 0.5rem 0.625rem;
    /* 8px, 10px in rem */
    font-size: 1rem;
    /* 16px in rem */
    color: white;
    background-color: #292E4E;
    border: none;
    border-radius: 0.188rem;
    /* 3px in rem */
    cursor: pointer;
}

.cancelInput {
    width: 30%;
    margin: 0.5rem 1rem;
    padding: 0.5rem 0.625rem;
    /* 8px, 10px in rem */
    font-size: 1rem;
    /* 16px in rem */
    color: white;
    background-color: #f33e4d;
    border: none;
    border-radius: 0.188rem;
    /* 3px in rem */
    cursor: pointer;
}

.colorPickerDiv {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 1rem 0rem;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.colorDiv {
    width: 3rem;
    height: 3rem;
    border-radius: 0.31rem;
    border: 1px solid;
    cursor: pointer;
    position: relative;
}

.colorDiv.selected::after {
    content: '\2713';
    color: #fff;
    font-size: 1.3rem;
    line-height: 1.3rem;
    text-align: center;
    /* background: #f96060; */
    width: 1.3rem;
    height: 1.3rem;
    /* border: 0.1rem solid #6074F900; */
    /* border-radius: 50%; */
    display: block;
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}